import React from 'react';
import styles from './LoginSignup.module.css'

const LoginSignup = ({login,signup}) => {
    return (
        <div className={styles.container}>
            <a href='#' onClick={login}><div className={styles.link}></div></a>
            <a href='#' onClick={signup}><div  className={styles.link}> </div></a>
            {/* <div className={styles.link}><a href='#' onClick={login}></a></div>
            <div className={styles.link}><a href='#'onClick={signup}> </a></div> */}
            {/* <button className={styles.btn} onClick={login}></button>
            <button className={styles.btn} onClick={signup}> </button> */}
           
        </div>
    );
};

export default LoginSignup;
